import React from 'react';
import { connect } from 'react-redux';
import { t } from '../../../i18n';
import Panel from '../../../components/Panel/Panel';
import AddFavoriteForm from '../../../components/AjoutFavoris/AjoutFavoris';
import { FAVORITES_NAMESPACE } from '../constants';
import DataTable from '../../../components/DataTable/CustomDataTable';
import { getFavoritesCashTransfer } from '../../../redux/cashTransfer/cashTransfer.actions';
import { getFavoritesTransfer } from '../../../redux/transfer/transfer.actions';
import { MultiSelect } from 'primereact/multiselect';
import FormModal from '../../../components/Ui/CustomModel/FormModal';

const AccountColumn = props => (
  <div>
    <span className="text-strong">
      {props.accountName}
      <span className="text-light">n° {props.accountNumber}</span>
    </span>
  </div>
);
const BeneficiaryColumn = props => (
  <span className="text-strong">
    {props.beneficiaryname}
    <br />
    <span className="text-light">ID : {props.beneficiaryNumber}</span>
  </span>
);

const FavoriteTypeColumn = props => (
  <span className="text-strong">{props.searchValue}</span>
);

const ActionsButtonsColumn = props => (
  <div>
    <button
      className="btn btn-secondary sm-mr-10 btn-sm"
      onClick={props.editBtnHandler}
    >
      <span className="actions_btn md-trigger popin_btn">
        {t(`${FAVORITES_NAMESPACE}editBtn`)}
      </span>
    </button>
    <button
      className="btn btn-clear text-alert btn-sm"
      onclick={props.transactionBtnHandler}
    >
      <span className="design-button md-trigger popin_btn">
        {props.transactionBtnLabel}
      </span>
    </button>
  </div>
);
let accounts = [];
class Favorites extends React.Component {
  constructor() {
    super();
    this.state = {
      loading: true,
      totalRecords: 0,
      showModal: false,
      accountUnique: null,
      uniqueType: null,
    };
  }

  componentDidMount() {
    this.props.getCashTransferFavorites();
    this.props.getTransferFavorites();
  }
  _toggleAddFavoriteModel = () => {
    this.setState(currentState => ({
      showModal: !currentState.showModal,
    }));
  };
  _buildDataTableRows = () => {
    let cashTransferRows = this.props.cashTransferFavorites.map(data => {
      const { clientAccount, beneficiary, label, amount, description } = data;
      accounts.push(clientAccount.accountName);
      return {
        clientName: clientAccount.accountName,
        clientNumber: clientAccount.accountNumber,
        beneficiaryName: beneficiary.name,
        beneficiaryNumber: beneficiary.accountNumber,
        type: t(`${FAVORITES_NAMESPACE}cashTransferTypeCln`),
        labelRow: label,
        amountRow: amount,
        descriptionRow: description,
        action: t(`${FAVORITES_NAMESPACE}cashTransferBtn`),
      };
    });
    let transferRows = this.props.transferFavorites.map(data => {
      const { clientAccount, beneficiary, label, amount, description } = data;
      accounts.push(clientAccount.accountName);
      return {
        clientName: clientAccount.accountName,
        clientNumber: clientAccount.accountNumber,
        beneficiaryName: beneficiary.name,
        beneficiaryNumber: beneficiary.accountNumber,
        type: t(`${FAVORITES_NAMESPACE}transferTypeCln`),
        labelRow: label,
        amountRow: amount,
        descriptionRow: description,
        action: t(`${FAVORITES_NAMESPACE}transferTypeCln`),
      };
    });
    return cashTransferRows.concat(transferRows);
  };

  _clientTemplate = (rowData, column) => (
    <AccountColumn
      accountName={rowData.clientName}
      accountNumber={rowData.clientNumber}
    />
  );
  _beneficiaryTemplate = (rowData, column) => (
    <BeneficiaryColumn
      beneficiaryname={rowData.beneficiaryName}
      beneficiaryNumber={rowData.beneficiaryNumber}
    />
  );
  _typeTemplate = (rowData, column) => (
    <FavoriteTypeColumn searchValue={rowData.type} />
  );
  _actionTemplate = (rowData, column) => {
    if (rowData.type === t(`${FAVORITES_NAMESPACE}transferBtn`)) {
      return (
        <ActionsButtonsColumn
          transactionBtnLabel={t(`${FAVORITES_NAMESPACE}transferBtn`)}
          editBtnHandler={this.onEditClickHandler}
          transactionBtnHandler={this.onVirementClickHandler}
        />
      );
    } else {
      return (
        <ActionsButtonsColumn
          transactionBtnLabel={t(`${FAVORITES_NAMESPACE}cashTransferBtn`)}
          editBtnHandler={this.onEditClickHandler}
          transactionBtnHandler={this.onTransferClickHandler}
        />
      );
    }
  };
  _onAccountUniqueChange = event => {
    this.dt.filter(event.value, 'clientName', 'in');
    this.setState({ accountUnique: event.value });
  };

  _onTypeUniqueChange = event => {
    this.dt.filter(event.value, 'type', 'in');
    this.setState({ uniqueType: event.value });
  };

  render() {
    let rows = this._buildDataTableRows();
    let uniqueAccount = accounts.filter(
      (value, index, array) => array.indexOf(value) === index,
    );
    let accountUnique = uniqueAccount.map(account => ({
      label: account,
      value: account,
    }));
    let uniqueType = [
      {
        label: 'all types',
        value: null,
      },
      {
        label: t(`${FAVORITES_NAMESPACE}transferBtn`),
        value: t(`${FAVORITES_NAMESPACE}transferBtn`),
      },
      {
        label: t(`${FAVORITES_NAMESPACE}cashTransferBtn`),
        value: t(`${FAVORITES_NAMESPACE}cashTransferBtn`),
      },
    ];
    let AccountFilter = (
      <MultiSelect
        style={{ width: '100%' }}
        value={this.state.accountUnique}
        options={accountUnique}
        onChange={this._onAccountUniqueChange}
      />
    );
    let TypeFilter = (
      <MultiSelect
        style={{ width: '100%' }}
        value={this.state.uniqueType}
        options={uniqueType}
        onChange={this.onTypeUniqueChange}
      />
    );
    return (
      <div className="row mx-auto">
        <div className="col-sm-12">
          <Panel
            title={t(`${FAVORITES_NAMESPACE}panelTitle`)}
            button={true}
            btntxt={t(`${FAVORITES_NAMESPACE}addFavoriteBtn`)}
            onclick={this._toggleAddFavoriteModel}
          >
            <DataTable
              value={rows}
              onRowReorder={e => (rows = e.value)}
              reference={el => (this.dt = el)}
              columnResizeMode="fit"
              cols={[
                {
                  field: 'clientName',
                  header: t(`${FAVORITES_NAMESPACE}accountClnLabel`),
                  body: this.clientTemplate,
                  filterElement: AccountFilter,
                  filter: true,
                },
                {
                  field: 'beneficiaryName',
                  header: t(`${FAVORITES_NAMESPACE}beneficiaryClnLabel`),
                  body: this.beneficiaryTemplate,
                },
                {
                  field: 'type',
                  header: t(`${FAVORITES_NAMESPACE}typeClnLabel`),
                  body: this.TypeTemplate,
                  filterElement: TypeFilter,
                  filter: true,
                },
                {
                  field: 'labelRow',
                  header: t(`${FAVORITES_NAMESPACE}labelClnLabel`),
                },
                {
                  field: 'amountRow',
                  header: t(`${FAVORITES_NAMESPACE}amountClnLabel`),
                },
                {
                  field: 'descriptionRow',
                  header: t(`${FAVORITES_NAMESPACE}descriptionClnLabel`),
                },
                {
                  field: 'action',
                  header: t(`${FAVORITES_NAMESPACE}actionClnLabel`),
                  body: this.ActionTemplate,
                },
              ]}
            />
          </Panel>
        </div>
        <FormModal
          title={t(`${FAVORITES_NAMESPACE}addFavoriteModelTitle`)}
          show={this.state.showModal}
          handleClose={this._toggleAddFavoriteModel}
        >
          <AddFavoriteForm />
        </FormModal>
      </div>
    );
  }
}

const mapDispatchToProps = dispatch => ({
  getCashTransferFavorites: () => {
    dispatch(getFavoritesCashTransfer());
  },
  getTransferFavorites: () => {
    dispatch(getFavoritesTransfer());
  },
});

const mapStateToProps = state => ({
  cashTransferFavorites: state.CashTransfer.favorites,
  transferFavorites: state.Transfer.favorites,
  isLoading: state.CashTransfer.isLoading || state.Transfer,
});

export default connect(
  mapStateToProps,
  mapDispatchToProps,
)(Favorites);
