import React, { Component } from 'react';
import { Calendar } from 'primereact/calendar';

export class DateCalendar extends Component {
  constructor() {
    super();
    this.state = {
      dates: null,
      show: false,
      selectedDates: null,
    };
  }

  handleDateChange = e => {
    this.setState({ selectedDates: e.value });
    this.props.onSelectDate({ selectedDates: e.value });
  };
  render() {
    let CalendarClass = `${this.state.show ? 'd-block' : ''}`;
    return (
      <>
        <span
          className="icon-calendrier"
          onClick={() => {
            this.setState({ show: !this.state.show });
          }}
        ></span>
        <div>
          <Calendar
            readOnlyInput={true}
            dateFormat="dd/mm/yy"
            className={CalendarClass}
            value={this.state.dates}
            onChange={e => {
              this.setState({ dates: e.value });
              this.props.handleDateChange(e);
            }}
            selectionMode="multiple"
            showIcon={true}
            maxDate={this.props.maxDate}
            showButtonBar={true}
          />
        </div>
      </>
    );
  }
}

export default DateCalendar;
