import React from 'react';
import { connect } from 'react-redux';
import { t } from '../../../i18n';
import Panel from '../../../components/Panel/Panel';
import BeneficiariesTable from '../../../components/DataTable/CustomDataTable.jsx';
import {
  getAllBeneficiaries,
  deleteBeneficiary,
} from '../../../redux/beneficiaries/beneficiaries.actions';
import { BENEFICIARIES_NAMESPACE } from '../constants';
import { FAVORITES_NAMESPACE } from '../constants';
import {
  showMessage,
  hideMessage,
} from '../../../redux/messages/messages.actions';

const ActionsButtonsColumn = props => (
  <div className="action-btn">
    <button className="btn btn-secondary btn-sm" onClick={props.onDelete}>
      <span className="actions_btn md-trigger popin_btn remove">
        {t(`${BENEFICIARIES_NAMESPACE}delete`)}
      </span>
    </button>
    <button className="btn btn-clear text-alert btn-sm" onClick={props.onEdit}>
      <span className="design-button md-trigger popin_btn editer">
        {t(`${BENEFICIARIES_NAMESPACE}edit`)}
      </span>
    </button>
  </div>
);
class Beneficiaries extends React.Component {
  constructor() {
    super();
    this.state = {
      loading: true,
      totalRecords: 0,
    };
  }
  componentDidMount() {
    this.props.getBeneficiaries();
  }
  _buildDataTableRows = () =>
    this.props.Beneficiaries.map(data => {
      const { beneficiaryId, name, accountNumber, mobileNumber, type } = data;
      return {
        beneficiaryId: beneficiaryId,
        beneficiaryName: name,
        beneficiaryNumber: accountNumber,
        mobileNumber: mobileNumber,
        type: type,
      };
    });
  _deleteBeneficiary = beneficiaryId => {
    this.props.deleteBeneficiary(beneficiaryId);
    this.props.hideMessage();
  };
  _actionTemplate = (beneficiary, column) => (
    <ActionsButtonsColumn
      onDelete={() => {
        this.props.showMessage(
          t(`${BENEFICIARIES_NAMESPACE}deletionConfirm`),
          'WARN',
          t(`${BENEFICIARIES_NAMESPACE}confirm`),
          true,
          () => this._deleteBeneficiary(beneficiary.beneficiaryId),
        );
      }}
      onEdit={() => {
        console.log('clicked Edit');
      }}
    />
  );
  render() {
    let rows = this._buildDataTableRows();
    return (
      <div className="row mx-auto">
        <div className="col-sm-12">
          <Panel title={t(`${BENEFICIARIES_NAMESPACE}beneficiariesList`)}>
            <BeneficiariesTable
              value={rows}
              onRowReorder={e => (rows = e.value)}
              reference={el => (this.dt = el)}
              columnResizeMode="fit"
              cols={[
                {
                  field: 'beneficiaryName',
                  header: t(`${BENEFICIARIES_NAMESPACE}beneficiaryName`),
                },
                {
                  field: 'beneficiaryNumber',
                  header: t(`${BENEFICIARIES_NAMESPACE}accountNumber`),
                },
                {
                  field: 'mobileNumber',
                  header: t(`${BENEFICIARIES_NAMESPACE}mobilePhone`),
                },
                {
                  field: 'type',
                  header: t(`${BENEFICIARIES_NAMESPACE}type`),
                },
                {
                  field: 'action',
                  header: t(`${BENEFICIARIES_NAMESPACE}actionLabel`),
                  body: this._actionTemplate,
                },
              ]}
            />
          </Panel>
        </div>
      </div>
    );
  }
}

const mapDispatchToProps = dispatch => ({
  getBeneficiaries: () => {
    dispatch(getAllBeneficiaries());
  },
  showMessage: (
    text,
    severity,
    validateTitle,
    hasCancelBtn,
    handleValidate,
  ) => {
    dispatch(
      showMessage(text, severity, validateTitle, hasCancelBtn, handleValidate),
    );
  },
  hideMessage: () => {
    dispatch(hideMessage());
  },
  deleteBeneficiary: beneficiaryId => {
    dispatch(deleteBeneficiary(beneficiaryId));
  },
});

const mapStateToProps = state => ({
  Beneficiaries: state.Beneficiaries.allBeneficiaries,
});

export default connect(
  mapStateToProps,
  mapDispatchToProps,
)(Beneficiaries);
